// src/components/StudentForm.js
import React, { useState } from 'react';
import axios from 'axios';
import jsPDF from 'jspdf'; //  import jsPDF
import './StudentForm.css';

function StudentForm() {
  const [name, setName] = useState('Zunaira');
  const [className, setClassName] = useState('Play Group');
  const [feePaid, setFeePaid] = useState(20000);
  const [balance, setBalance] = useState(2000);
  const [date, setDate] = useState('2025-05-04');

  const handleClear = () => {
    setName('');
    setClassName('');
    setFeePaid('');
    setBalance('');
    setDate('');
  };

  //  Download PDF before/after submitting
  const downloadReceipt = () => {
    const doc = new jsPDF();

    doc.setFontSize(16);
    doc.text('Astra Preschool - Fee Receipt', 20, 20);

    doc.setFontSize(12);
    doc.text(`Student Name: ${name}`, 20, 40);
    doc.text(`Class: ${className}`, 20, 50);
    doc.text(`Fees Paid: ${feePaid}`, 20, 60);
    doc.text(`Balance: ${balance}`, 20, 70);
    doc.text(`Date: ${new Date(date).toLocaleDateString()}`, 20, 80);

    doc.save(`${name}_Fee_Receipt.pdf`);
  };

  const handleSubmit = async (e) => {
    e.preventDefault();

    const studentData = {
      name,
      className,
      feePaid,
      balance,
      date,
    };

    try {
      const res = await axios.post('http://localhost:5000/api/students', studentData);
      alert('Student added successfully!');
      console.log(res.data);
      handleClear();
    } catch (error) {
      console.error('Error adding student:', error);
      alert('Failed to add student.');
    }
  };

  return (
    <form className="student-form" onSubmit={handleSubmit}>
      <hr />
      <div className="row">
        <label>Name:</label>
        <input type="text" value={name} onChange={(e) => setName(e.target.value)} />
        <label>Class:</label>
        <select value={className} onChange={(e) => setClassName(e.target.value)}>
          <option>Play Group</option>
          <option>Nursery</option>
          <option>LKG</option>
          <option>UKG</option>
        </select>
      </div>

      <div className="row">
        <label>Fee Paid:</label>
        <input type="number" value={feePaid} onChange={(e) => setFeePaid(e.target.value)} />
        <label>Balance:</label>
        <input type="number" value={balance} onChange={(e) => setBalance(e.target.value)} />
      </div>

      <div className="row">
        <label>Date:</label>
        <input type="date" value={date} onChange={(e) => setDate(e.target.value)} />
        <div className="pdf-button">
          <button type="button" onClick={downloadReceipt}>Download-Receipt</button>
        </div>
      </div>

      <div className="buttons">
        <button className="submit" type="submit">Submit</button>
        <button className="clear" type="button" onClick={handleClear}>Clear</button>
      </div>
    </form>
  );
}

export default StudentForm;
