// src/components/StudentList.js
import jsPDF from "jspdf";
import React, { useEffect, useState } from 'react';
import axios from 'axios';
import './StudentList.css'; // optional styling

function StudentList() {
  const [students, setStudents] = useState([]);

  useEffect(() => {
    // Fetch students on component mount
    const fetchStudents = async () => {
      try {
        const res = await axios.get('http://localhost:5000/api/students');
        setStudents(res.data);
      } catch (error) {
        console.error('Error fetching students:', error);
      }
    };

    fetchStudents();
  }, []);

  // Function to generate and download PDF receipt
  const downloadReceipt = (student) => {
    const doc = new jsPDF();

    doc.setFontSize(16);
    doc.text("Astra Preschool - Fee Receipt", 20, 20);

    doc.setFontSize(12);
    doc.text(`Student Name: ${student.name}`, 20, 40);
    doc.text(`Age: ${student.age || "N/A"}`, 20, 50); // Fallback if age is missing
    doc.text(`Class: ${student.className}`, 20, 60);
    doc.text(`Fees Paid: ${student.feePaid}`, 20, 70);
    doc.text(`Balance: ${student.balance}`, 20, 80);
    doc.text(`Date: ${new Date(student.date).toLocaleDateString()}`, 20, 90);

    doc.save(`${student.name}_Fee_Receipt.pdf`);
  };

  return (
    <div className="student-list">
      <h2>All Students</h2>
      <table>
        <thead>
          <tr>
            <th>Name</th>
            <th>Class</th>
            <th>Fee Paid</th>
            <th>Balance</th>
            <th>Date</th>
            <th>Receipt</th>
          </tr>
        </thead>
        <tbody>
          {students.map((s, index) => (
            <tr key={index}>
              <td>{s.name}</td>
              <td>{s.className}</td>
              <td>{s.feePaid}</td>
              <td>{s.balance}</td>
              <td>{new Date(s.date).toLocaleDateString()}</td>
              <td>
                <button onClick={() => downloadReceipt(s)}>
                  Download Fee Receipt
                </button>
              </td>
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  );
}

export default StudentList;
